import {
  Modal,
  Container,
  Row,
  Button,
  Form,
  Nav,
  Dropdown,
  DropdownButton,
} from "react-bootstrap";
import React from "react";
import Sha256 from "crypto-js/sha256";
import Hex from "crypto-js/enc-hex";
import {
  getMeta,
  getState,
  getProfile,
  getProfileLists,
  getPreviewLink,
} from "./storage";
import NFTCard from "./NFTCard";
import {
  updateProfile,
  updateCollection,
  queryCollectionByOwner,
  queryUnassignedNftsByOwner,
  getBusy,
  setCollectionItem,
} from "./api";
import GetWalletModal from "./GetWalletModal";
import CollectionCard from "./CollectionCard";
import EditCollectionModal from "./EditCollectionModal";
import { useParams, useLocation } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { getCollectionItems, getCollection } from "./api";
import { faPlus } from "@fortawesome/free-solid-svg-icons";

function ProfileController() {
  const { profileAddress, collectionHandle, nftAddress } = useParams();
  const location = useLocation();
  const view =
    profileAddress !== undefined
      ? location.pathname.endsWith("/owned")
        ? "profileOwned"
        : "profile"
      : collectionHandle !== undefined
      ? "collection"
      : nftAddress !== undefined
      ? location.pathname.endsWith("/owned")
        ? "nftOwned"
        : "nft"
      : "unknown";

  // Variables always required.
  const [editShow, setEditShow] = React.useState(null);
  const [profile, setProfile] = React.useState(null);

  // For profile view.
  const [createCollectionShow, setCreateCollectionShow] = React.useState(false);
  const [collections, setCollections] = React.useState(null);
  const [unassignedList, setUnassignedList] = React.useState(null);
  const [profileLists, setProfileLists] = React.useState(null);

  // For collection view.
  const [collection, setCollection] = React.useState(null);
  const [collectionItems, setCollectionItems] = React.useState(null);

  // For NFT view.
  const [nftMeta, setNftMeta] = React.useState(null);
  const [nftState, setNftState] = React.useState(null);
  const [nftPreviewLink, setNftPreviewLink] = React.useState(null);
  const [nftBusy, setNftBusy] = React.useState(null);

  const [editProfileName, setEditProfileName] = React.useState("");
  const editProfileNameRef = React.createRef();
  const [getWalletShow, setGetWalletShow] = React.useState(false);

  // Initialize for profile view.
  React.useEffect(() => {
    if (view === "profile") {
      (async () => {
        // Retrieve and set profile.
        let profile = await getProfile(profileAddress);
        if (profile === null) {
          profile = {};
        }
        profile.address = profileAddress;
        setProfile(profile);

        // Retrieve and set collections.
        const collections = await queryCollectionByOwner({
          owner: profileAddress,
        });
        if (collections !== null) {
          setCollections(collections.reverse());
        }

        // Retrieve and set unassigned list.
        const unassignedList = await queryUnassignedNftsByOwner({
          owner: profileAddress,
        });
        setUnassignedList(unassignedList);

        if (profile !== null) {
          if (profile.name === null) {
            document.title = `Profile | ${profileAddress}`;
          } else {
            document.title = `Profile | ${profile.name}`;
          }
        }
      })();
    }
  }, []);

  // Initialize for profileOwned view.
  React.useEffect(() => {
    if (view === "profileOwned") {
      (async () => {
        // Retrieve and set profile.
        let profile = await getProfile(profileAddress);
        if (profile === null) {
          profile = {};
        }
        profile.address = profileAddress;
        setProfile(profile);

        // Retrieve and set profile lists.
        const profileLists = await getProfileLists(profileAddress);

        setProfileLists({
          createdNfts: profileLists.createdNfts.reverse(),
          ownedNfts: profileLists.ownedNfts.reverse(),
          historicalNfts: profileLists.historicalNfts.reverse(),
        });

        if (profile !== null) {
          if (profile.name === null) {
            document.title = `Profile | ${profileAddress}`;
          } else {
            document.title = `Profile | ${profile.name}`;
          }
        }
      })();
    }
  }, []);

  // Initialize for collection view.
  React.useEffect(() => {
    (async () => {
      if (view === "collection") {
        const collection = await getCollection({ handle: collectionHandle });
        setCollection(collection);
        let profile = await getProfile(collection.owner);
        if (profile === null) {
          profile = {};
        }
        profile.address = collection.owner;
        setProfile(profile);
        setCollectionItems(
          await getCollectionItems({ handle: collectionHandle })
        );
      }
    })();
  }, []);

  // Initialize for nft view.
  React.useEffect(() => {
    (async () => {
      if (view === "nft") {
        const nftMeta = await getMeta(nftAddress);
        setNftMeta(nftMeta);
        const nftState = await getState(nftAddress);
        setNftState(nftState);
        const nftPreviewLink = await getPreviewLink(nftAddress);
        setNftPreviewLink(nftPreviewLink);

        if (nftMeta !== null) {
          document.title = `NFT | ${nftMeta.name}`;
          let profile = await getProfile(nftState.creator);
          if (profile === null) {
            profile = {};
          }
          profile.address = nftState.creator;
          setProfile(profile);
        }

        setNftBusy(await getBusy({ address: nftAddress }));
      }
    })();
  }, []);

  const doEditProfileSubmit = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();

    if (window.dingo === undefined) {
      setGetWalletShow(true);
      return;
    }

    const payload = {
      timestamp: Date.now(),
      owner: profileAddress,
      name:
        editProfileName === null || editProfileName === ""
          ? null
          : editProfileName,
    };
    const message = JSON.stringify(payload);

    const signResult = await window.dingo.requestSign(
      Hex.stringify(Sha256(message))
    );
    if (signResult.error !== undefined) {
      return;
    }
    payload.signature = signResult.result;

    const updateResult = await updateProfile(payload);
    if ("error" in updateResult) {
      return;
    }

    window.location.reload(false);
  };

  const doCreateCollectionSubmit = async (profile) => {
    // Create payload and signature message.
    const payload = {
      timestamp: Date.now(),
      owner: profileAddress,
      handle: profile.handle,
      name: profile.name,
      thumbnail: profile.thumbnail,
      description: profile.description,
    };
    const message = JSON.stringify(payload);

    // Sign.
    const signResult = await window.dingo.requestSign(
      Hex.stringify(Sha256(message))
    );
    if (signResult.error !== undefined) {
      return;
    }
    payload.signature = signResult.result;

    // Send.
    const updateResult = await updateCollection(payload);
    if ("error" in updateResult) {
      return;
    }

    window.location.reload(false);
  };

  const doMove = async (address, handle) => {
    // Create payload and signature message.
    const payload = {
      timestamp: Date.now(),
      address: address,
      handle: handle,
    };
    const message = JSON.stringify(payload);

    // Sign.
    const signResult = await window.dingo.requestSign(
      Hex.stringify(Sha256(message))
    );
    if (signResult.error !== undefined) {
      return;
    }
    payload.signature = signResult.result;

    // Send.
    const updateResult = await setCollectionItem(payload);
    if ("error" in updateResult) {
      return;
    }

    window.location.reload(false);
  };

  return (
    <div className="profile-container justify-content-center mx-auto">
      <Container>
        <Row>
          <div className="profile-section profile-header">
            <h3>
              {profile === null
                ? "-"
                : profile.name === "" ||
                  profile.name === null ||
                  profile.name === undefined
                ? "Unnamed Profile"
                : profile.name}
            </h3>
            <h6>{profile !== null && profile.address}</h6>
            <span
              className="clickable"
              style={{ display: "inline" }}
              onClick={() => setEditShow(true)}
            >
              (Edit)
            </span>
            <hr />
            <Nav variant="tabs">
              <Nav.Item>
                <Nav.Link
                  eventKey="creations"
                  active={
                    view === "profile" ||
                    view === "collection" ||
                    view === "nft"
                  }
                  href={profile !== null && `/profile/${profile.address}/`}
                >
                  NFT creations
                </Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link
                  eventKey="unassigned"
                  active={view === "profileOwned" || view === "nftOwned"}
                  href={profile !== null && `/profile/${profile.address}/owned`}
                >
                  Owned NFTs
                </Nav.Link>
              </Nav.Item>
            </Nav>
          </div>
        </Row>

        {view === "profile" && (
          <Row>
            <div className="profile-section profile-content mt-3 py-3">
              <div className="d-flex flex-row content-header">
                <span className="me-auto content-title accent">
                  Collections
                </span>
                <span
                  className="clickable"
                  onClick={() => setCreateCollectionShow(true)}
                >
                  <FontAwesomeIcon className="me-1" icon={faPlus} /> Create new
                </span>
              </div>
              <ul className="collection-cards-container mt-4">
                {collections !== null &&
                  collections.map((x) => (
                    <a key={x} href={`/collection/${x}`}>
                      <CollectionCard handle={x} />
                    </a>
                  ))}
              </ul>
            </div>
          </Row>
        )}

        {view === "profile" &&
          unassignedList !== null &&
          unassignedList.length > 0 && (
            <Row>
              <div className="profile-section profile-content mt-3 py-3">
                <div className="d-flex flex-row content-header">
                  <span className="me-auto content-title accent">
                    Unassigned NFTs
                  </span>
                </div>
                <ul className="cards-container mt-4">
                  {unassignedList.map((x) => (
                    <div key={x}>
                      <a href={`/nft/${x}`}>
                        <NFTCard address={x} />
                      </a>
                      <DropdownButton
                        className="mt-3 px-4"
                        title="Move to collection"
                      >
                        {collections !== null &&
                          collections.map((y) => (
                            <Dropdown.Item key={y} onClick={() => doMove(x, y)}>
                              {y}
                            </Dropdown.Item>
                          ))}
                      </DropdownButton>
                    </div>
                  ))}
                </ul>
              </div>
            </Row>
          )}

        {view === "profileOwned" &&
          profileLists !== null &&
          profileLists.ownedNfts.filter(
            (x) => !profileLists.createdNfts.some((y) => y === x)
          ).length > 0 && (
            <Row>
              <div className="profile-section profile-content mt-3 py-3">
                <div className="d-flex flex-row content-header">
                  <span className="me-auto content-title accent">
                    Owned NFTs
                  </span>
                </div>
                <ul className="cards-container mt-4">
                  {profileLists.ownedNfts
                    .filter(
                      (x) => !profileLists.createdNfts.some((y) => y === x)
                    )
                    .map((x) => (
                      <a key={x} href={`/nft/${x}`}>
                        <NFTCard address={x} />
                      </a>
                    ))}
                </ul>
              </div>
            </Row>
          )}

        {view === "profileOwned" &&
          profileLists !== null &&
          profileLists.historicalNfts.filter(
            (x) => !profileLists.createdNfts.some((y) => y === x)
          ).length > 0 && (
            <Row>
              <div className="profile-section profile-content mt-3 py-3">
                <div className="d-flex flex-row content-header">
                  <span className="me-auto content-title accent">
                    Previously owned NFTs
                  </span>
                </div>
                <ul className="cards-container mt-4">
                  {profileLists.historicalNfts
                    .filter(
                      (x) => !profileLists.createdNfts.some((y) => y === x)
                    )
                    .map((x) => (
                      <a key={x} href={`/nft/${x}`}>
                        <NFTCard address={x} />
                      </a>
                    ))}
                </ul>
              </div>
            </Row>
          )}

        {view === "collection" && (
          <Row>
            <div className="profile-section profile-content mt-3 py-3">
              <div className="d-flex flex-row content-header">
                <span className="content-title me-auto">
                  {collection !== null && collection.name}
                </span>
              </div>
              <ul className="cards-container mt-4">
                {collectionItems !== null &&
                  collectionItems.map((x) => (
                    <a key={x} href={`/nft/${x}`}>
                      <NFTCard address={x} />
                    </a>
                  ))}
              </ul>
            </div>
          </Row>
        )}

        {view === "nft" && (
          <Row>
            <div className="profile-section profile-content mt-3 py-3">
              <div className="d-flex flex-column nft-content">
                <img src={nftPreviewLink} />
                <div
                  className="me-auto d-flex flex-column justify-content-center"
                  style={{ alignItems: "center", textAlign: "center" }}
                >
                  <span>Apple asdasdasd</span>
                </div>
              </div>
            </div>
          </Row>
        )}
      </Container>

      <Modal
        size="lg"
        centered
        show={editShow}
        onHide={() => setEditShow(false)}
        onEntered={() => editProfileNameRef.current.focus()}
      >
        <Modal.Header closeButton>
          <Modal.Title>Edit profile</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={doEditProfileSubmit}>
            <Form.Group className="mb-3">
              <Form.Label>Profile name (leave blank if no change)</Form.Label>
              <Form.Control
                type="text"
                value={editProfileName}
                placeholder="Max 40 characters"
                onChange={(e) => setEditProfileName(e.target.value)}
                isValid={editProfileName !== "" && editProfileName.length <= 40}
                isInvalid={
                  editProfileName !== "" && editProfileName.length > 40
                }
                ref={editProfileNameRef}
              />
              {editProfileName.length > 40 && (
                <div>
                  <Form.Text className="input-error">Name too long</Form.Text>
                </div>
              )}
            </Form.Group>

            <div style={{ textAlign: "center" }}>
              <Button
                className="popup-button mb-3 mt-3 px-4"
                variant="primary"
                type="submit"
              >
                Verify and update profile
              </Button>
            </div>
          </Form>
        </Modal.Body>
      </Modal>

      <GetWalletModal
        show={getWalletShow}
        onHide={() => setGetWalletShow(false)}
      />

      <EditCollectionModal
        title="Create new collection"
        show={createCollectionShow}
        owner={profileAddress}
        onHide={() => setCreateCollectionShow(false)}
        onSubmit={doCreateCollectionSubmit}
      />
    </div>
  );
}

export default ProfileController;
